/* layout */
header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}
header .l-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1vw 4vw;
  transition: all 0.3s;
}
@media screen and (max-width: 1080px) {
  header .l-header {
    display: none;
  }
}
header .l-header a {
  width: 100%;
  cursor: pointer;
}
header .l-header__logo {
  width: 20%;
}
header .l-header__logo img {
  width: 80px;
  height: 64px;
}
header .l-header__nav {
  width: 70%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 1rem;
}
header .l-header__nav-list {
  margin: 0 0 0 1.5rem;
}

.l-header__ham-logo {
  width: 20%;
  position: fixed;
  top: 30px;
  left: 30px;
  z-index: -1;
}
.l-header__ham-logo img {
  width: 80px;
  height: 64px;
}
@media screen and (min-width: 1079px) {
  .l-header__ham {
    display: none;
  }
}
.l-header__ham #menu__toggle {
  opacity: 0;
}
.l-header__ham #menu__toggle:checked ~ .menu__btn {
  border: 1px solid #fff;
}
.l-header__ham #menu__toggle:checked ~ .menu__btn > span {
  transform: rotate(45deg);
  width: 35px;
  position: absolute;
  right: 7px;
}
.l-header__ham #menu__toggle:checked ~ .menu__btn > span::before {
  top: 0;
  transform: rotate(0);
  background: #fff;
}
.l-header__ham #menu__toggle:checked ~ .menu__btn > span::after {
  top: 0;
  transform: rotate(90deg);
  background: #fff;
}
.l-header__ham #menu__toggle:checked ~ .menu__content {
  visibility: visible;
  right: 0;
  z-index: -1;
}
.l-header__ham .menu__btn {
  display: flex;
  align-items: center;
  position: fixed;
  top: 25px;
  right: 25px;
  width: 50px;
  height: 50px;
  border: 1px solid #2F9B9B;
  cursor: pointer;
  z-index: 999;
}
.l-header__ham .menu__btn .menu__btn__bar, .l-header__ham .menu__btn .menu__btn__bar::before, .l-header__ham .menu__btn .menu__btn__bar::after {
  display: block;
  position: absolute;
  right: 7px;
  width: 100%;
  width: 35px;
  height: 1px;
  background-color: #2F9B9B;
  transition-duration: 0.5s;
}
.l-header__ham .menu__btn .menu__btn__bar::before, .l-header__ham .menu__btn .menu__btn__bar::after {
  content: "";
}
.l-header__ham .menu__btn .menu__btn__bar::before {
  top: -8px;
  left: 0;
  content: "";
}
.l-header__ham .menu__btn .menu__btn__bar::after {
  top: 8px;
  left: 0;
  content: "";
}
.l-header__ham .menu__content {
  position: fixed;
  visibility: hidden;
  top: 0;
  width: 300px;
  height: 100%;
  display: block;
  right: -100%;
  margin: 0;
  padding: 12rem 0;
  list-style: none;
  background-color: #2F9B9B;
  box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
  transition-duration: 0.5s;
}
.l-header__ham .menu__content-text {
  display: block;
  padding: 12px 34px;
  transition-duration: 0.25s;
}
.l-header__ham .menu__content-text a {
  text-decoration: none;
  color: #fff;
  font-size: 1.4rem;
}

